import { CloseIcon } from '@chakra-ui/icons'
import { Box } from '@chakra-ui/react'
import React from 'react'

export default function UserBadgeItem({user,handleFunction}) {
  return (
    <Box
      px={2}
      py={1}
      borderRadius="lg"
      m={1}
      mb={2}
      variant="solid"
      fontSize={12}
      backgroundColor="#1d6d7a"
      color="white"
      cursor="pointer"
    >
      {user.name}
      {/* unselect user*/}
      <CloseIcon pl={1} onClick={handleFunction} />
    </Box>
  );
}
